body {
  background-color: #eee; }

p {
  font-size: 18px;
  line-height: 1.5;
  font-weight: 300; }

.gray-section {
  padding-top: 130px;
  text-align: center; }

.white-section {
  padding-top: 130px;
  text-align: center;
  background-color: white; }

#page-header {
  background-color: black;
  background-image: url("https://media.giphy.com/media/rtTSkaaU6vI1G/source.gif");
  /* Set a specific height */
  height: 650px;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center; }
  #page-header h1 {
    color: white;
    font-weight: bold;
    font-size: 5em;
    font-family: 'Lobster Two', cursive;
    color: #dc3545; }
  #page-header p {
    color: white; }
  #page-header > div {
    display: flex;
    flex-direction: column;
    height: 85%;
    justify-content: space-around; }

#portrait-container {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  margin-top: 50px;
  padding-left: 125px;
  padding-right: 125px; }
  #portrait-container .thumbnail {
    width: 350px; }

#thesis {
  padding-top: 50px;
  width: 80%;
  margin: 0 auto; }
  #thesis div {
    padding: 30px;
    font-size: 1.5em !important; }

.example .well {
  width: 80%;
  margin: 0 auto;
  padding: 30px; }

a.red {
  color: #7d1a0d;
  font-weight: bold;
  text-decoration: none; }
  a.red:hover {
    color: #dc3545;
    text-decoration: none; }

.image-tabs > div > ul {
  display: flex;
  justify-content: center;
  border-color: #7d1a0d !important; }
  .image-tabs > div > ul li {
    font-size: 18px;
    float: none; }
    .image-tabs > div > ul li a {
      background-color: inherit !important;
      color: black; }
      .image-tabs > div > ul li a:hover {
        border-color: #f5f5f5 !important;
        border-bottom-color: #7d1a0d !important;
        background-color: #f5f5f5 !important; }
  .image-tabs > div > ul li.active > a {
    background-color: inherit !important;
    border-color: #7d1a0d !important;
    border-bottom-color: #eee !important;
    color: black !important; }

.image-tabs .flex-container {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #7d1a0d;
  padding-bottom: 40px; }

.image-tabs .analysis {
  width: 60%;
  text-align: left; }
  .image-tabs .analysis h3 {
    margin-left: 20px; }
  .image-tabs .analysis ul li {
    margin-bottom: 5px; }

#flames {
  margin-top: 150px;
  background-image: url("http://www.scootersgroupcanada.com/images/fire.png");
  background-size: contain;
  background-repeat: repeat-x;
  background-position-y: 5px;
  background-position-x: -5px; }

#works-cited {
  text-align: left !important;
  max-width: 900px;
  margin: 0 auto; }

#white-tab li.active a {
  border-bottom-color: white !important; }
